import ArrayHelper from "../helpers/ArrayHelper";
import PartyStatistics from "../models/PartyStatistics";

export default class MockPartyService {
    getParty(): PartyStatistics {
        return {
            reputation: 12,
            traits: ["Humiliated the Dread Pirate Roberts", "Gouged the eye from a Titan"],
        };
    }

    private static _inventoryItemIds: string[] = ["wolf_spear"];
    private static _stashItemIds: string[] = ["hag_teeth", "token_of_servitude"];

    getInventory(): string[] {
        return MockPartyService._inventoryItemIds;
    }

    setInventory(itemIds: string[]) {
        MockPartyService._inventoryItemIds = itemIds;
    }

    addInventoryItem(id: string) {
        MockPartyService._inventoryItemIds.push(id);
    }

    removeInventoryItem(id: string) {
        ArrayHelper.remove(MockPartyService._inventoryItemIds, id);
    }

    getStash(): string[] {
        return MockPartyService._stashItemIds;
    }

    setStash(itemIds: string[]) {
        MockPartyService._stashItemIds = itemIds;
    }

    addStashItem(id: string) {
        MockPartyService._stashItemIds.push(id);
    }

    removeStashItem(id: string) {
        ArrayHelper.remove(MockPartyService._stashItemIds, id);
    }
}
